
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

a{
    text-decoration: none;
    color: inherit;
}

ul, li{
    list-style: none;
}

body{
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    background-color: var(--black);
    color: var(--white);
}



